.container {
    display: flex;
    min-height: 80%;
    flex-wrap: wrap;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
  }
  
  @media (min-width: 1100px) {
    .container {
      padding-left: 100px;
      padding-right: 100px;
      /* width: 400px; */
      width: 1100px;
    }
  }

  .container p {
    color: white!important;
    height: 10%;
    text-align: center;
    font-size: 2em;
    overflow-wrap: break-word;
  }